<template>
  <div class="home_page" id="home_page">
    <div class="home_page" @click="to_stories">
      <div class="text_part" id="text_part">
        <div class="idea_box" id="idea_box">
          <div class="idea_bg" id="idea_bg"></div>
          <div
            class="idea_letter"
            id="idea_letter"
            v-for="item in idea"
            :key="item.idx"
            :style="{ visibility: item.visibility }"
          >
            {{ item.letter }}
          </div>
        </div>

        <div class="check_box" id="check_box">
<!--          {{ global.lang_pack.check }}-->
          <img class="check_img" src="static/home/check.png">
        </div>
      </div>

      <div class="guide_part" id="guide_part">
        <div class="guide_box" id="guide_box">
          <div class="guide_ball" id="guide_ball"></div>
        </div>
      </div>
    </div>

    <Menu></Menu>
    <LangTransfer v-if="global.mode === 1"></LangTransfer>
  </div>
</template>

<style lang="css">

body {
  background: #f7f7f7;
}

div {
  color: #251714;
}

::-webkit-scrollbar {
  display: none;
}

div.home_page {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
}

div.text_part {
  position: fixed;
  transform: translate(-50%, -50%);
  top: 50%;
  left: 50%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

div.idea_box {
  display: flex;
  padding-left: 10rem;
  padding-right: 10rem;
  flex-direction: row;
}

div.idea_bg {
  position: fixed;
  z-index: -1;
  height: 130rem;
  background: #efcd24;
  width: 0;
  right: calc(0rem);
}

div.idea_letter {
  padding-left: 8rem;
  padding-right: 8rem;
  font-family: blk;
  text-transform: uppercase;
  font-size: 120rem;
  display: inline-block;
  height: 130rem;
  display: flex;
  align-items: center;
  justify-content: center;
}

div.check_box {
  position: relative;
  top: 50rem;
  font-weight: normal;
  text-transform: uppercase;
  font-size: 100rem;
}

img.check_img{
  width: 400rem;
}

div.guide_part {
  position: fixed;
  bottom: calc(60rem);
  left: 50%;
  transform: translate(-50%, 0);
}

div.guide_box {
  height: calc(80rem);
  width: calc(10rem);
  border-radius: calc(10rem);
}

div.guide_ball {
  position: relative;
  background: #efcd24;
  height: calc(10rem);
  width: calc(10rem);
  border-radius: calc(10rem);
}

/*手机版样式*/
@media screen and (max-width: 540px){
  div.idea_letter {
    font-size: 80rem;
    height: 80rem;
    padding-left: 4rem;
    padding-right: 4rem;
  }

  div.check_box {
    top: 0;
  }

  img.check_img{
    width: 220rem;
  }

  div.guide_part {
    bottom: 120rem;
    left: 50%;
    transform: translate(-50%, 0);
  }

  div.idea_bg {
    height: 80rem;
  }

  div.guide_box {
    width: 18rem;
    border-radius:18rem;
  }

  div.guide_ball {
    height: 18rem;
    width: 18rem;
    border-radius: 18rem;
  }
}
</style>

<script>
import global from "../global";
import anime from "../../../modules/anime";
import Logo from "../../components/logo/Logo";
import Menu from "../../components/menu/Menu";
import LangTransfer from "../../components/lang_transfer/LangTransfer";

let idea_anime_end = false;
let guide_anime;

//export default
export default {
  name: "Home",
  data() {
    return {
      global: global,
      idea: [],
    };
  },
  components: {
    Logo,
    Menu,
    LangTransfer
  },
  beforeCreate() {
    //set documentElement root size and language package
    global.change_root_size();
    global.set_lang_pack();
  },
  created() {
  },
  mounted() {
    // mount the window size listen
    window.onresize = () => {
      return (() => {
        global.change_root_size();
      })();
    };
    // console.log(Logo.methods)
    Logo.methods.change_color("#251714", "#efcd24", 0);
    this.start_idea_anime();
    this.start_guide_anime();
    document.getElementById("home_page").addEventListener('wheel', this.handleScroll);
  },
  destroyed() {
    idea_anime_end = true;
  },
  watch: {

  },
  methods: {
    start_idea_anime() {
      let that = this;
      let idx = 0;
      let idea_list = this.global.lang_pack.home_idea_list;
      idea_anime_end = false;
      let change_fun = function () {
        let idea = idea_list[idx];
        idx++;
        idx %= 3;
        that.idea = [];
        if(document.getElementById("idea_bg") === null)
          return
        document.getElementById("idea_bg").style.width = "0px";
        for (let i = 0; i < idea.length; i++) {
          that.idea.push({
            idx: i,
            letter: idea[i],
            visibility: "hidden",
          });
        }

        for (let i = 0; i < idea.length; i++) {
          setTimeout(() => {
            that.idea[i].visibility = "visible";
            if (i === idea.length - 1) {
              if(document.getElementById("idea_box") === null)
                return
              let bg_width = document.getElementById("idea_box").clientWidth;
              new anime({
                targets: "div.idea_bg",
                width: bg_width + "px",
                delay: 100,
                duration: idea.length * 100,
                easing: "linear",
              });
            }
          }, i * 150);
        }

        if (idea_anime_end === false) {
          setTimeout(() => {
            change_fun();
          }, idea.length * 250 + 200);
        }
      };

      change_fun();
    },
    start_guide_anime() {
      new anime({
        targets: "div.guide_ball",
        opacity: [
          {value: ["0%", "100%"], duration: 250},
          {value: ["100%", "0%"], delay: 750, duration: 250},
        ],
        scaleY: [
          {value: 2, duration: 250, delay: 250},
          {value: 1, duration: 250},
        ],
        scaleX: [
          {value: 0.5, duration: 250, delay: 250},
          {value: 1, duration: 250},
        ],
        translateY: [
          {
            value: "30rem",
            duration: 250,
            delay: 250,
          },
          {
            value: "70rem",
            duration: 250,
          },
        ],
        // direction: "alternate",
        endDelay: 250,
        loop: true,
        easing: "linear",
      });
    },
    handleScroll(e) {
      this.scroll = document.documentElement.scrollTop || document.body.scrollTop
      if (e.deltaY > 0)
        this.to_stories()
    },

    to_stories() {
      this.$router.push("/Stories");
    }
  },
};
</script>



